'use client';

import { AnalysisResponse, FlaggedWord } from '@/types';
import { useState } from 'react';

interface IssuesPanelProps {
  analysisResult: AnalysisResponse | null;
  isAnalyzing: boolean;
  onWordReplace: (originalWord: string, newWord: string) => void;
  onIssueClick?: (issue: FlaggedWord) => void;
}

export function IssuesPanel({ 
  analysisResult, 
  isAnalyzing, 
  onWordReplace,
  onIssueClick 
}: IssuesPanelProps) {
  const [expandedIssues, setExpandedIssues] = useState<Set<number>>(new Set());

  const toggleIssueExpansion = (index: number) => {
    const newExpanded = new Set(expandedIssues);
    if (newExpanded.has(index)) {
      newExpanded.delete(index);
    } else {
      newExpanded.add(index);
    }
    setExpandedIssues(newExpanded);
  };

  const getCategoryColor = (category: string): string => {
    const colors = {
      'hate_speech': 'bg-red-100 text-red-800',
      'gender_bias': 'bg-pink-100 text-pink-800',
      'racial_bias': 'bg-orange-100 text-orange-800',
      'age_bias': 'bg-purple-100 text-purple-800',
      'disability_bias': 'bg-indigo-100 text-indigo-800',
      'religious_bias': 'bg-blue-100 text-blue-800',
      'general_bias': 'bg-gray-100 text-gray-800'
    };
    return colors[category as keyof typeof colors] || 'bg-gray-100 text-gray-800';
  };

  const getConfidenceColor = (confidence: number): string => {
    if (confidence > 0.8) return 'text-red-600';
    if (confidence > 0.6) return 'text-orange-600';
    if (confidence > 0.4) return 'text-yellow-600';
    return 'text-green-600';
  };

  if (isAnalyzing) {
    return (
      <div className="w-full lg:w-96 bg-white border-l border-gray-200 p-6">
        <div className="flex items-center justify-center h-32">
          <div className="text-center">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto mb-4"></div>
            <p className="text-gray-600 text-sm">Analyzing text...</p>
          </div>
        </div>
      </div>
    );
  }

  if (!analysisResult) {
    return (
      <div className="w-full lg:w-96 bg-white border-l border-gray-200 p-6">
        <div className="text-center text-gray-500 mt-8">
          <div className="mb-4">
            <svg className="mx-auto h-12 w-12 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
          </div>
          <p className="text-sm">Enter text and analyze to see detected issues</p>
        </div>
      </div>
    );
  }

  const { flaggedWords, confidence } = analysisResult;

  return (
    <div className="w-full lg:w-96 h-full bg-white border-l border-gray-200 p-6 overflow-y-auto custom-scrollbar">
      <div className="sticky top-0 bg-white pb-4 border-b border-gray-200 mb-4">
        <h2 className="text-lg font-semibold text-gray-900 mb-2">
          Analysis Results
        </h2>
        
        {/* Overall Score */}
        <div className="bg-gray-50 rounded-lg p-3 mb-3">
          <div className="flex items-center justify-between">
            <span className="text-sm font-medium text-gray-700">
              Confidence:
            </span>
            <span className={`text-lg font-bold ${getConfidenceColor(confidence || 0)}`}>
              {Math.round((confidence || 0) * 100)}%
            </span>
          </div>
        </div>

        {/* Summary */}
        <div className="text-sm text-gray-600">
          {flaggedWords.length === 0 && (
            <div className="text-center py-2">
              <span className="text-green-600 font-medium">✓ No issues detected</span>
            </div>
          )}
          {flaggedWords.length > 0 && (
            <div className="flex items-center justify-between">
              <span>{flaggedWords.length} issue{flaggedWords.length > 1 ? 's' : ''} found</span>
              <button
                onClick={() => setExpandedIssues(new Set())}
                className="text-blue-600 hover:text-blue-800 text-xs"
              >
                Collapse all
              </button>
            </div>
          )}
        </div>
      </div>

      {/* Issues List */}
      <div className="space-y-3">
        {flaggedWords.map((issue, index) => {
          const isExpanded = expandedIssues.has(index);
          
          return (
            <div 
              key={index}
              className="border border-gray-200 rounded-lg overflow-hidden hover:shadow-sm transition-shadow"
            >
              {/* Issue Header */}
              <div 
                className="p-4 cursor-pointer hover:bg-gray-50"
                onClick={() => {
                  toggleIssueExpansion(index);
                  onIssueClick?.(issue);
                }}
              >
                <div className="flex items-start justify-between mb-2">
                  <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getCategoryColor(issue.category)}`}>
                    {issue.category.replace('_', ' ')}
                  </span>
                  <div className="flex items-center gap-2">
                    <span className={`text-xs font-medium ${getConfidenceColor(issue.confidence)}`}>
                      {Math.round(issue.confidence * 100)}%
                    </span>
                    <svg
                      className={`w-4 h-4 text-gray-400 transition-transform ${isExpanded ? 'rotate-180' : ''}`}
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                    >
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                    </svg>
                  </div>
                </div>
                
                <div className="mb-2">
                  <span className="text-sm bg-yellow-100 px-2 py-1 rounded font-medium">
                    "{issue.word}"
                  </span>
                </div>
                
                {issue.explanation && (
                  <div className="text-xs text-gray-600 line-clamp-2">
                    {issue.explanation}
                  </div>
                )}
              </div>

              {/* Expanded Content */}
              {isExpanded && (
                <div className="px-4 pb-4 border-t border-gray-100">
                  {issue.explanation && (
                    <div className="mb-3 text-sm text-gray-700">
                      <span className="font-medium">Why this was flagged:</span>
                      <p className="mt-1 text-gray-600">{issue.explanation}</p>
                    </div>
                  )}
                  
                  {issue.suggestions && issue.suggestions.length > 0 && (
                    <div>
                      <span className="text-sm font-medium text-gray-700 mb-2 block">
                        Suggested alternatives:
                      </span>
                      <div className="space-y-2">
                        {issue.suggestions.map((suggestion, idx) => (
                          <button
                            key={idx}
                            className="w-full text-left p-2 rounded bg-blue-50 hover:bg-blue-100 border border-blue-200 transition-colors"
                            onClick={(e) => {
                              e.stopPropagation();
                              onWordReplace(issue.word, suggestion.word);
                            }}
                          >
                            <div className="flex items-center justify-between">
                              <span className="text-sm font-medium text-blue-900">
                                "{suggestion.word}"
                              </span>
                              <span className="text-xs text-blue-700">
                                {Math.round(suggestion.confidence * 100)}%
                              </span>
                            </div>
                            {suggestion.reason && (
                              <p className="text-xs text-blue-700 mt-1">
                                {suggestion.reason}
                              </p>
                            )}
                          </button>
                        ))}
                      </div>
                    </div>
                  )}
                </div>
              )}
            </div>
          );
        })}
      </div>
    </div>
  );
} 