/**
 * API Service Layer for Bias Detection App
 * 
 * This module handles all communication with the backend API.
 * It provides a clean interface for our React components to use.
 */

import { AnalysisRequest, AnalysisResponse } from '@/types';

// Configuration for API endpoints
const API_CONFIG = {
  baseUrl: process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000',
  endpoints: {
    analyze: '/analyze',
    health: '/health'
  },
  timeout: 30000, // 30 seconds timeout
} as const;

/**
 * Custom error class for API-related errors
 * This helps us handle different types of errors appropriately
 */
export class ApiError extends Error {
  constructor(
    message: string,
    public status?: number,
    public code?: string
  ) {
    super(message);
    this.name = 'ApiError';
  }
}

/**
 * Generic API request function with error handling
 * This is a utility function that handles common API patterns
 */
async function apiRequest<T>(
  endpoint: string,
  options: RequestInit = {}
): Promise<T> {
  const url = `${API_CONFIG.baseUrl}${endpoint}`;
  
  // Set default headers
  const defaultHeaders = {
    'Content-Type': 'application/json',
    'Accept': 'application/json',
  };

  try {
    const response = await fetch(url, {
      ...options,
      headers: {
        ...defaultHeaders,
        ...options.headers,
      },
    });

    // Check if response is ok
    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      throw new ApiError(
        errorData.message || `HTTP ${response.status}: ${response.statusText}`,
        response.status,
        errorData.code
      );
    }

    return await response.json();
  } catch (error) {
    // Handle network errors
    if (error instanceof ApiError) {
      throw error;
    }
    
    throw new ApiError(
      error instanceof Error ? error.message : 'Network error occurred',
      0,
      'NETWORK_ERROR'
    );
  }
}

/**
 * Analyze text for bias and hate speech
 * 
 * @param request - The analysis request containing text and options
 * @returns Promise resolving to analysis results
 */
export async function analyzeText(
  request: AnalysisRequest
): Promise<AnalysisResponse> {
  // For development/demo purposes, use mock data
  // In production, this would make a real API call
  if (process.env.NODE_ENV === 'development' || !process.env.NEXT_PUBLIC_API_URL) {
    // Simulate API delay
    await new Promise(resolve => setTimeout(resolve, 1000 + Math.random() * 1000));
    return createMockAnalysisResponse(request.text);
  }

  return apiRequest<AnalysisResponse>(API_CONFIG.endpoints.analyze, {
    method: 'POST',
    body: JSON.stringify(request),
  });
}

/**
 * Check if the API is healthy and responding
 * Useful for showing connection status to users
 */
export async function checkApiHealth(): Promise<{ status: string; timestamp: string }> {
  return apiRequest<{ status: string; timestamp: string }>(
    API_CONFIG.endpoints.health,
    { method: 'GET' }
  );
}

/**
 * Utility function to validate text input
 * Checks for common issues before sending to API
 */
export function validateTextInput(text: string): {
  isValid: boolean;
  errors: string[];
} {
  const errors: string[] = [];
  
  if (!text.trim()) {
    errors.push('Text cannot be empty');
  }
  
  if (text.length > 10000) {
    errors.push('Text is too long (maximum 10,000 characters)');
  }
  
  if (text.length < 5) {
    errors.push('Text is too short (minimum 5 characters)');
  }
  
  return {
    isValid: errors.length === 0,
    errors
  };
}

/**
 * Create a mock analysis response for development/testing
 * This is useful when the backend isn't available
 */
export function createMockAnalysisResponse(text: string): AnalysisResponse {
  // Simple mock that flags potentially problematic words
  const mockWords = ['stupid', 'dumb', 'crazy', 'insane', 'lame'];
  const flaggedWords = mockWords
    .map(word => {
      const index = text.toLowerCase().indexOf(word);
      if (index === -1) return null;
      
      return {
        word,
        startIndex: index,
        endIndex: index + word.length,
        category: 'general_bias' as const,
        confidence: 0.75,
        suggestions: [
          { word: 'unclear', confidence: 0.8, reason: 'More neutral language' },
          { word: 'confusing', confidence: 0.7, reason: 'Describes the issue without judgment' },
        ],
        explanation: 'This word may be perceived as ableist or judgmental'
      };
    })
    .filter((item): item is NonNullable<typeof item> => item !== null);

  return {
    originalText: text,
    flaggedWords,
    analysisId: `mock-${Date.now()}`,
    timestamp: new Date().toISOString(),
    confidence: 0.85
  };
} 